nav {
    background-color: transparent;
    height: 400px;
    width:100%;
    margin:0;
  }
nav ul {
    list-style-type: none;
    margin:unset;
    padding:unset;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    text-align:center;
    
  }
nav ul li a {
    margin-bottom:15px;
    text-decoration:none;
    color:white;
    font-size:20px;
    padding:5px;
  }
nav ul li a:hover {
    color:red;
  }
a.active {
  color:gold;
}
.friends {
  color:white;
  
}
.friends ul {
  list-style-type: none;
  margin:0;
  padding:0;
  display:flex;
  flex-wrap:wrap;
  justify-content: space-around;
}
.friends ul li {
  padding:3px;
}
.friends ul li img {
  width:40px;
  height: 40px;
  border-radius: 100%;
}